.nft-diagonal-grid {
  position: relative;
  width: 100%;
  min-height: 500px;
  padding: 20px;
  overflow: hidden;
}

.nft-item {
  position: absolute;
  width: 200px;
  height: 200px;
  transition: all 0.3s ease;
}

.nft-item:hover {
  transform: scale(1.1) translateZ(20px) !important;
  z-index: 1000 !important;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
}

.nft-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}